<template>
	<div class="videoDetail">
		<van-nav-bar class="nav-bar" title="新闻详情" left-text="返回" left-arrow @click-left="onClickLeft" />
		<video width="100%" height="100%" controls="controls" :src="data.Mp4Link" autoplay="autoplay">
		</video>
		<div class="videoDetailcontent">
			<p>{{data.Title}}</p>
			<p><span>{{data.CategoryName}}</span><span style="margin-left: 20px;">{{data.CreatedOn}}</span></p>
			<p><span>播放数:{{data.TotalVisit}}</span><span style="margin-left:20px">时长:{{data.Duration}}</span><span style="margin-left:20px">评论:0</span></p>
		</div>
		<div class="bkgray"></div>
	</div>
</template>

<script>
	export default {
		computed: {
			data() {
				return this.$route.query.item
			}
		},
		methods: {
			onClickLeft() {
				this.$router.go(-1)
			}
		}
	}
</script>

<style>
	.bkgray {
		background-color: #f5f5f5;
		width: 100%;
		height: 10px;
	}
	.videoDetailcontent {
		padding: 12px 15px;
		color: #999999;
		font-size: 1px;

	}

	.van-nav-bar__title,
	.van-nav-bar .van-icon,
	.van-nav-bar__text {
		color: #FFFFFF !important;
	}

	.videoDetail .nav-bar {
		background: #5A99F6;
	}
</style>
